import React from 'react'
import { dateFormate } from '../../../utils/common'

const CorrectionCards = ({correctionData}) => {
  return (
    <div className="timeCountmdl correctionTime">
    <div className="w-25 ">
        <p>
            Attendance Status <b>{correctionData?.attendance_status}</b>
        </p>
        <strong className="w-100">
            <span>{correctionData?.total_working_hours}</span>
            <small> Net Working Time </small>
        </strong>
    </div>
    <div className="w-25 ">
        <p>
            Date <b>{dateFormate(correctionData?.attendance_date)}</b>
        </p>
        <strong className="w-100 h-30">
            <span>{correctionData?.duration}</span>
            <small> Total Login Hours</small>
        </strong>
    </div>
    <div className="w-25 ">
        <p>
            Shift Start Time
        </p>
        <strong className="w-100">
            <span>{correctionData?.shift_start_time}</span>
            <small>  Shift Start Time </small>
        </strong>
    </div>
    <div className="w-25 ">
        <p>
            Shift End Time
        </p>
        <strong className="w-100">
            <span>{correctionData?.shift_end_time}</span>
            <small>  Shift End Time </small>
        </strong>
    </div>
</div>
  )
}

export default CorrectionCards
