import React, { useEffect, useState } from 'react'
import { CustomButton, CustomError, CustomSpinner } from '../../globalComponents'
import { Input } from '../../commanComponent'
import { useCustomTranslation } from '../../../translation'
import CustomSelect from '../../commanComponent/CustomSelect'
import moment from 'moment'
import { useDispatch, useSelector } from 'react-redux'
import { attendanceCorrectionActions, leaveAction } from '../../../core/resource/actions'
import BlankTableError from '../../commanComponent/BlankTableError'
import CorrectionCards from '../attendanceCorrectionApplication/CorrectionCards'
import { dateFormate } from '../../../utils/common'

const AttendanceLogs = (props) => {
    const { handleOpenClose, openModel, viewModalType, selectedCalenderData, setUpdatedLogsData } = props
    const [allLeaveConstantData, setAllLeaveConstantData] = useState('')


    const dispatch = useDispatch()
    const {
        getAttendanceLogsPending,
        getAttendanceLogsRes,
        getAttendanceLogsError,

        updateAttendanceLogPending,
        updateAttendanceLogRes,
        updateAttendanceLogError,

    } = useSelector(state => state.attendanceCorrection)
    const {
        getLeaveConstantDataPending,
        getLeaveConstantDataRes,
        getLeaveConstantDataError,
    } = useSelector(state => state.leave);

    const [attendanceLogs, setAttendanceLogs] = useState([])
    const { t } = useCustomTranslation()
    const [errors, setErrors] = useState([])

    useEffect(() => {
        getAttendanceLogs()
        getLeaveConstantData()
    }, [])

    /* Get Attendance Logs */
    const getAttendanceLogs = () => {
        const payload ={
            date :selectedCalenderData?.attendance_date
        }
        dispatch(attendanceCorrectionActions.getAttendanceLogsRequest(payload))

    }
    useEffect(() => {
        if (!getAttendanceLogsPending) {
            if (getAttendanceLogsError) {
                return;
            }
            if (getAttendanceLogsRes) {
                if (getAttendanceLogsRes?.code === 200) {
                    setAttendanceLogs(getAttendanceLogsRes?.data)
                }
            }
        }
    }, [
        getAttendanceLogsRes,
        getAttendanceLogsError,
        getAttendanceLogsPending
    ]);

    /* Get Attendance Logs */
    const updateLogs = () => {
        const logs = [...attendanceLogs]
        const updatedLogsData = logs.map(item => ({
            ...item,
            device_from: item?.activityName ? item?.activityName?.value : item?.device_from,
            device_from_name: item?.activityName ? item?.activityName?.label : item?.device_from_name,
            log_type: item?.checkInOut ? item?.checkInOut?.value : item?.log_type,
            punch_time: `${item.punch_date} ${item.punch_time}`

        }));
        let data = {
            employeeLogs: updatedLogsData,
            date: moment().format('YYYY-MM-DD')
        }
        dispatch(attendanceCorrectionActions.updateAttendanceLogRequest(data))

    }
    useEffect(() => {
        if (!updateAttendanceLogPending) {
            if (updateAttendanceLogError) {
                dispatch(attendanceCorrectionActions?.handleClearCorrectionRequest("HANDLE_CLEAR_UPDATE_LOG"))
                return;
            }
            if (updateAttendanceLogRes) {
                if (updateAttendanceLogRes?.code === 200) {
                    console.log(updateAttendanceLogRes?.data, "updateAttendanceLogRes?.data")
                    setUpdatedLogsData(updateAttendanceLogRes?.data)
                    handleOpenClose(viewModalType ? viewModalType : '')
                    dispatch(attendanceCorrectionActions?.handleClearCorrectionRequest("HANDLE_CLEAR_UPDATE_LOG"))
                }
            }
        }
    }, [
        updateAttendanceLogRes,
        updateAttendanceLogError,
        updateAttendanceLogPending
    ]);



    const editAttendanceRow = index => {
        setAttendanceLogs(prevLogs =>
            prevLogs.map((log, i) =>
                i === index
                    ? {
                        ...log,
                        isEdit: !log.isEdit,
                        activityName: { label: log.device_from_name, value: log.device_from },
                        checkInOut: { label: log.log_type, value: log.log_type }
                    }
                    : log
            )
        );
    };

    const revertChanges = index => {
        let newAttendanceLogArray = [...attendanceLogs];
        let newObj = newAttendanceLogArray[index];
        delete newObj?.isEdit
        delete newObj?.reason
        delete newObj?.requestedTime
        newAttendanceLogArray[index] = newObj;
        setAttendanceLogs(newAttendanceLogArray);
        setErrors([])
        // setRequestedTime('');
        // setReason('');
    };

    const deleteAttendanceRow = index => {
        setAttendanceLogs((pre) => {
            return pre.filter((_, ind) => ind !== index)
        })
    }

    const handleFromChange = (e, fieldName, index) => {
        const name = fieldName || e.target?.name;
        const { value, type, checked } = e?.target ? e.target : { value: e };
        setAttendanceLogs(prevState => {
            const updatedLogs = [...prevState];
            updatedLogs[index] = {
                ...updatedLogs[index],
                [name]: value,
            };
            return updatedLogs;
        });
        setErrors(prevState => {
            const updatedError = [...prevState];
            if (updatedError[index]) {
                delete updatedError[index][name];
                if (Object.keys(updatedError[index]).length === 0) {
                    updatedError[index] = undefined;
                }
            }
            return updatedError;
        });
    };
    const saveEditedRow = (index) => {
        const checkValidAdd = handleCheckRowAdd(index)
        console.log(index, "index", checkValidAdd)
        if (!checkValidAdd) {
            let newAttendanceLogArray = [...attendanceLogs];
            let newObj = newAttendanceLogArray[index];
            newObj = {
                ...newObj,
                isEdit: false
            }
            newAttendanceLogArray[index] = newObj;
            setAttendanceLogs(newAttendanceLogArray)
        } else {

        }
        return checkValidAdd

    }
    // const handleCheckRowAdd = (index) => {
    //     const tempAttendanceLogs = attendanceLogs[index]
    //     if (!tempAttendanceLogs?.requestedTime) {
    //         setErrors(prevState => {
    //             const updatedError = [...prevState];
    //             updatedError[index] = {
    //                 ...updatedError[index],
    //                 ["requestedTime"]: "Requested time can not be blank",
    //             };
    //             return updatedError;
    //         });
    //     }
    //     if(!tempAttendanceLogs?.reason){
    //         setErrors(prevState => {
    //             const updatedError = [...prevState];
    //             updatedError[index] = {
    //                 ...updatedError[index],
    //                 ["reason"]: "Reason can not be blank",
    //             };
    //             return updatedError;
    //         });
    //     }

    // }

    const handleCheckRowAdd = (index) => {
        const tempAttendanceLogs = attendanceLogs[index];
        let hasError = false;

        const updatedError = [...errors];

        if (!tempAttendanceLogs?.requestedTime) {
            updatedError[index] = {
                ...updatedError[index],
                requestedTime: true,
            };
            hasError = true;
        }

        if (!tempAttendanceLogs?.reason) {
            updatedError[index] = {
                ...updatedError[index],
                reason: true,
            };
            hasError = true;
        }
        if (!tempAttendanceLogs?.activityName) {
            updatedError[index] = {
                ...updatedError[index],
                activityName: true,
            };
            hasError = true;
        }
        if (!tempAttendanceLogs?.checkInOut) {
            updatedError[index] = {
                ...updatedError[index],
                checkInOut: true,
            };
            hasError = true;
        }

        setErrors(updatedError);
        return hasError;
    };


    // useEffect(()=>{
    //     return(()=>{
    //         setAttendanceLogs([])
    //         setErrors([])
    //     })
    // },[openModel])

    const addEvent = () => {
        let tempAttendanceLogsData = [...attendanceLogs]
        const logsObj = {
            date: moment().format("DD-MM-YYYY"),
            time: '',
            activityName: '',
            checkInOut: '',
            isEdit: true
        }
        tempAttendanceLogsData.unshift(logsObj)
        setAttendanceLogs(tempAttendanceLogsData)
    }


    const selectStyle = (index, fieldName) => {
        return {
            control: (provided) => ({
                ...provided,
                border: errors[index]?.[fieldName] ? '1px solid #E44438' : '',

            })
        }
    }


    const renderAttendanceLogs = () => {
        return (
            attendanceLogs ? attendanceLogs.map((item, index) => <tr key={index}>
                <td>{dateFormate(item?.punch_date)}</td>
                <td>{item?.punch_time}</td>
                <td><CustomSelect
                    data={allLeaveConstantData?.activityData}
                    placeholder={t("placeholders.select")}
                    value={item?.activityName || { label: item?.device_from_name, value: item?.device_from }}
                    handleChange={(e) => handleFromChange(e, "activityName", index)}
                    name="activityName"
                    isDisabled={item?.isEdit ? false : true}
                    styles={selectStyle(index, "activityName")}
                />
                </td>
                <td>
                    <CustomSelect
                        data={allLeaveConstantData?.inOutData}
                        placeholder={t("placeholders.select")}
                        value={item?.checkInOut || { label: item?.log_type, value: item?.log_type }}
                        handleChange={(e) => handleFromChange(e, "checkInOut", index)}
                        name="checkInOut"
                        isDisabled={item?.isEdit ? false : true}
                        styles={selectStyle(index, "activityName")}
                    />
                </td>
                <td>
                    <Input
                        type="time"
                        value={item?.requestedTime}
                        name="requestedTime"
                        onChange={(e) => handleFromChange(e, "", index)}
                        min="09:30:00"
                        step="2"
                        readOnly={item?.isEdit ? false : true}
                        style={{
                            border: errors[index]?.requestedTime ? "1px solid #E44438" : "",
                        }}
                    />
                </td>
                <td>
                    <Input
                        type="text"
                        name="reason"
                        value={item?.reason}
                        placeholder="Enter Reason"
                        onChange={(e) => handleFromChange(e, "", index)}
                        readOnly={item?.isEdit ? false : true}
                        style={{
                            border: errors[index]?.reason ? "1px solid #E44438" : "",
                        }}
                    />
                </td>
                <td>
                    <div className="d-flex align-items-center gap-2 justify-content-end actionTd">
                        {item?.isEdit ? (
                            <>
                                <button type="button" className="bg-transparent border-0 p-0" onClick={() => saveEditedRow(index)}>
                                    <img src="images/save-icon.svg" alt="" width={13} />
                                </button>
                                <button type="button" className="bg-transparent border-0 p-0"
                                    onClick={() => revertChanges(index)}
                                >
                                    <img src="images/cancel-icon.svg" alt="" width={11} />
                                </button>
                            </>) : (
                            <button type="button" className="bg-transparent border-0 p-0"
                                onClick={() => editAttendanceRow(index)}
                            >
                                <img src="images/edit-icon2.svg" alt="" width={12} />
                            </button>)}
                        <button type="button" className="bg-transparent border-0 p-0" onClick={() => deleteAttendanceRow(index)}>
                            <img src="images/delete-icon.svg" alt="" width={12} />
                        </button>
                    </div>
                </td>
            </tr>
            ) : (
                !getAttendanceLogsPending && <BlankTableError totalField={7} />
            )
        )

    }

    /* Get Leave Constant Data */
    const getLeaveConstantData = (params) => {
        dispatch(leaveAction.getLeaveConstantDataRequest(params));
    }
    useEffect(() => {
        if (!getLeaveConstantDataPending) {
            if (getLeaveConstantDataError) {
                return;
            }
            if (getLeaveConstantDataRes) {
                if (getLeaveConstantDataRes?.code === 200) {
                    setAllLeaveConstantData(getLeaveConstantDataRes?.data)
                }
            }
        }
    }, [
        getLeaveConstantDataRes,
        getLeaveConstantDataError,
        getLeaveConstantDataPending
    ]);

    return (
        <div
            className={`offcanvas offcanvas-end applyLeaveCanvas allBreakCanvas ${openModel ? 'show' : ''}`}
            tabIndex={-1}
            id="allBreakCanvas"
            style={{
                visibility: openModel ? 'visible' : 'hidden',
                width: "750px"
            }}
        >
            <div className="offcanvas-header">
                <h5 className="offcanvas-title  fs-6 fw-bold text-primary">All Break</h5>
                <button
                    type="button"
                    className="btn-close"
                    data-bs-dismiss="offcanvas"
                    aria-label="Close"
                    onClick={() => { handleOpenClose(viewModalType ? viewModalType : '') }}
                />
            </div>
            <div className="offcanvas-body">
                <div className="formPanel">
                    <CorrectionCards correctionData={selectedCalenderData} />
                    <div className="d-flex align-items-center justify-content-between mb-3">
                        <strong className="fs-7 text-primary">
                            <span className="fw-normal">Corrected break :</span>
                            00:00:00
                        </strong>
                        <span className="fs-8 text-primary">
                            <img src="images/add-event.svg" alt="" width={14} className="me-2" />
                            <u style={{ cursor: "pointer" }} onClick={() => addEvent()}>Add Event</u>
                        </span>
                    </div>
                    <div className="tableDiv">
                        <table className="table">
                            <thead>
                                <tr>
                                    <th>Date</th>
                                    <th>Time</th>
                                    <th>Activity Name</th>
                                    <th>IN/OUT</th>
                                    <th>Requested Time</th>
                                    <th>Reason</th>
                                    <th>Action</th>
                                </tr>
                            </thead>
                            <tbody>
                                {getAttendanceLogsPending ? <CustomSpinner isShow={getAttendanceLogsPending} size="xl" /> : renderAttendanceLogs()}

                            </tbody>
                        </table>
                    </div>
                </div>
            </div>
            <div className="offcanvas-footer">
                <div className="d-flex align-items-center justify-content-end gap-3">

                    <CustomButton
                        classNames="border-0 p-0 bg-transparent cancelBtn"
                        handleClick={() => handleOpenClose()}
                        text={"Back"}
                    />
                    <CustomButton
                        classNames="btn btn-org-gradient"
                        text={"Update"}
                        handleClick={() => updateLogs()}
                    />

                </div>
            </div>
        </div>

    )
}

export default AttendanceLogs
