import React, { useState, useEffect } from 'react'
import { CustomButton, CustomDateRangePicker, CustomError } from '../../globalComponents'
import { useDispatch, useSelector } from 'react-redux'
import { leaveAction } from '../../../core/resource/actions';
import { toast } from 'react-toastify';
import { addDays } from 'date-fns';
import { DateRangePicker, DateRange } from 'react-date-range';
import 'react-date-range/dist/styles.css'; // main css file
import 'react-date-range/dist/theme/default.css'; // theme css file
import './LeaveCalendar.css';
import { enUS } from 'date-fns/locale';
import moment from 'moment';
import { validateSchema } from '../../../utils/ValidationHelper';
import { applyLeaveSchema } from '../../../utils/ValidationSchema';
import ApproveRejectModal from '../../modal/ApproveRejectModal';

const LeaveApplication = (props) => {
    const dispatch = useDispatch()
    const { openModel, openLeaveApplicationModel } = props

    const { applyLeavePending,
        applyLeaveRes,
        applyLeaveError,

        getAllEmployeeLeavePending,
        getAllEmployeeLeaveRes,
        getAllEmployeeLeaveError,

        sandwichLeavesDatesPending,
        sandwichLeavesDatesRes,
        sandwichLeavesDatesError,

        getLeaveBalancePending,
        getLeaveBalanceRes,
        getLeaveBalanceError,

    } = useSelector(state => state.leave)
    const [startDate, setStartDate] = useState(moment().format('YYYY-MM-DD'));
    const [endDate, setEndDate] = useState(moment().format('YYYY-MM-DD'));
    const [leaveType, setLeaveType] = useState()
    const [reason, setReason] = useState('')
    const [errors, setErrors] = useState({});
    const [applyLeaveModel, setApplyLeaveModel] = useState(false)
    const [getAllEmployeeInLeave, setAllEmployeeInLeave] = useState([])
    const [sandwichDates, setSandWichDates] = useState([])
    const [leaveBalance, setLeaveBalance] = useState([])
    const [state, setState] = useState([
        {
            startDate: new Date(),
            endDate: new Date(),
            key: 'selection'
        }
    ]);

    const [leaveDetails, setLeaveDetails] = useState([
        { leave_date: '', leave_type: '', leave_status: '' },
    ]);
    const [holiday, setHoliya] = useState([
        { holiday_date: '2024-10-08', holiday_type: 'full_day' },
        { holiday_date: '2024-10-15', holiday_type: 'full_day' },
        { holiday_date: '2024-10-20', holiday_type: 'first_half' },
    ])
    const setLeaveTypefun = (data) => {
        resetFunction()
        setLeaveType(data)
    }
    const handleRadioChange = (index, value) => {
        const updatedLeaveDetails = leaveDetails.map((item, i) =>
            i === index ? { ...item, leave_status: value } : item
        );
        setLeaveDetails(updatedLeaveDetails);
    };

    const handleRangeChange = (item) => {
        const { startDate, endDate } = item.selection;
        setState([item.selection]);
        setStartDate(moment(startDate).format('YYYY-MM-DD'));
        setEndDate(moment(endDate).format('YYYY-MM-DD'));
    };

    useEffect(() => {
        getLeaveBalance()
    }, [openModel])
    useEffect(() => {
        createLeave()
    }, [endDate])

    const getEmpOnleaveinDaterange = (start, end) => {
        let data = {
            start_date: start,
            end_date: end
        }
        dispatch(leaveAction.getAllEmployeeLeaveRequest(data))
    }

    const createLeave = () => {
        const start = new Date(moment(startDate).format('MM-DD-YYYY'));
        const end = new Date(moment(endDate).format('MM-DD-YYYY'));
        let loop = new Date(start);
        let i = 0;
        let newArr = [{ leave_date: '', leave_type: '', leave_status: '' }];
        while (loop <= end) {
            // let a = moment(loop);
            // let b = moment(new Date());
            var day = moment(loop).day();
            if (
                day === 1 ||
                day === 2 ||
                day === 3 ||
                day === 4 ||
                day === 5 ||
                day === 6 ||
                day === 0
            ) {
                if (holiday.some(item => moment(item.holiday_date).format("YYYY-MM-DD") == moment(loop).format('YYYY-MM-DD'))) {
                    let getHoliday = holiday.filter(item => moment(item.holiday_date).format('DD-MM-YYYY') == moment(loop).format('DD-MM-YYYY'))
                    newArr[i].leave_date = moment(loop).format('YYYY-MM-DD');
                    newArr[i].leave_type = "holiday"
                    newArr[i].leave_status = getHoliday[0].holiday_type;
                    setLeaveDetails(newArr);
                    let newArr1 = newArr;
                    newArr = [
                        ...newArr1,
                        { leave_date: '', leave_type: '', leave_status: '' },
                    ];
                    let newDate = loop.setDate(loop.getDate() + 1);
                    loop = new Date(newDate);
                    i = i + 1;
                } else {
                    newArr[i].leave_date = moment(loop).format('YYYY-MM-DD');
                    newArr[i].leave_type = leaveType;
                    newArr[i].leave_status = "Full Day";
                    setLeaveDetails(newArr);
                    let newArr1 = newArr;
                    newArr = [
                        ...newArr1,
                        { leave_date: '', leave_type: '', leave_status: '' },
                    ];
                    let newDate = loop.setDate(loop.getDate() + 1);
                    loop = new Date(newDate);
                    i = i + 1;
                }
            } else {
                let newDate1 = loop.setDate(loop.getDate() + 1);
                loop = new Date(newDate1);
            }
        }
    };
    useEffect(() => {
        if (!applyLeavePending) {
            if (applyLeaveError) {
                return;
            }
            if (applyLeaveRes) {
                if (applyLeaveRes?.code === 200) {
                    resetFunction()
                    getLeaveBalance()
                    dispatch(leaveAction.handleClearLeaveStore("HANDLE_CLEAR_APPLY_LEAVE_STORE"))
                    openLeaveApplicationModel()
                    setLeaveBalance([])

                }
            }
        }
    }, [
        applyLeaveRes,
        applyLeaveError,
        applyLeavePending
    ]);

    useEffect(() => {
        if (!getAllEmployeeLeavePending) {
            if (getAllEmployeeLeaveError) {
                return;
            }
            if (getAllEmployeeLeaveRes) {
                if (getAllEmployeeLeaveRes?.code === 200) {
                    setAllEmployeeInLeave(getAllEmployeeLeaveRes?.data)
                }
            }
        }
    }, [
        applyLeaveRes,
        getAllEmployeeLeaveError,
        getAllEmployeeLeavePending
    ]);

    useEffect(() => {
        if (!sandwichLeavesDatesPending) {
            if (sandwichLeavesDatesError) {
                return;
            }
            if (sandwichLeavesDatesRes) {
                if (sandwichLeavesDatesRes?.code === 200) {
                    dispatch(leaveAction.handleClearLeaveStore("HANDLE_SANDWICH_LEAVE_STORE"))
                    if (sandwichLeavesDatesRes.data !== null) {
                        setApplyLeaveModel(!applyLeaveModel)
                        setSandWichDates(sandwichLeavesDatesRes.data)
                    } else {
                        setApplyLeaveModel(!applyLeaveModel)
                    }
                }
            }
        }
    }, [
        sandwichLeavesDatesRes,
        sandwichLeavesDatesError,
        sandwichLeavesDatesPending
    ]);

    const submitApplyLeave = async (type) => {
        if (type) {
            let leaveData = leaveDetails.filter(item => item.leave_type !== "holiday" && item.leave_type !== "")
            let data = {
                leaveDetails: leaveData,
                reason: reason
            }
            const validationResponse = await validateSchema(applyLeaveSchema, data);
            if (validationResponse.isValid) {
                dispatch(leaveAction.applyLeaveRequest(data))
                setApplyLeaveModel(!applyLeaveModel)
                setErrors({})
            } else {
                setErrors(validationResponse.errors);
            }
        } else {
            setApplyLeaveModel(!applyLeaveModel)
        }
    }

    const closeButton = () => {
        openLeaveApplicationModel()
        setLeaveBalance([])
        resetFunction()
    }
    const resetFunction = () => {
        console.log("Close")
        setLeaveType()
        setReason('')
        setStartDate(' ')
        setEndDate(' ')
        setLeaveDetails([{
            leave_date: '', leave_type: '', leave_status: ''
        }])
        setState([{
            startDate: new Date(),
            endDate: new Date(),
            key: 'selection'
        }])
    }
    const submitApplyLeavefun = () => {
        let data = {
            leaveDetails: leaveDetails
        }
        dispatch(leaveAction.sandwichLeavesDatesRequest(data))
    }

    /*Get Leave Balance Api call  */
    const getLeaveBalance = () => {
        dispatch(leaveAction.getLeaveBalanceRequest());
    }

    useEffect(() => {
        if (!getLeaveBalancePending) {
            if (getLeaveBalanceError) {
                return;
            }
            if (getLeaveBalanceRes) {
                if (getLeaveBalanceRes?.code === 200) {
                    const getLeaveTypeListResData = getLeaveBalanceRes?.data
                    setLeaveBalance(getLeaveTypeListResData)
                }
            }
        }
    }, [
        getLeaveBalanceRes,
        getLeaveBalanceError,
        getLeaveBalancePending
    ]);
    return (
        <>
            <div className={`offcanvas offcanvas-end applyLeaveCanvas ${openModel ? 'show' : ''}`}

                tabIndex={-1}
                style={{
                    visibility: openModel ? 'visible' : 'hidden',
                    width: "800px"
                }}
            >
                <div className="offcanvas-header">
                    <h5 className="offcanvas-title  fs-5 fw-bold text-primary">Leave Application</h5>
                    <button type="button" className="btn-close" data-bs-dismiss="offcanvas" aria-label="Close"
                        onClick={() => closeButton()}
                    />
                </div>
                <div className="offcanvas-body">
                    <div className="formPanel">
                        <div className="form-group mb-3">
                            <label for="" className="text-primary"><i className="bi bi-calendar2-range-fill pe-2"></i> Leave
                                Type</label>
                            <ul className="typeCheckList">
                                {leaveBalance && leaveBalance?.map((item, index) => (
                                    <>{
                                        <li>
                                            <div className="typeCheck">
                                                <input type="radio" id={`type${index + 1}`} value={item?.leave_type} name='leave_type' onChange={(e) => setLeaveTypefun(e.target.value)} className="d-none" disabled={item?.leave_balance == '0'} />
                                                <label for={`type${index + 1}`}>{item?.leave_name} ({item?.leave_balance}) <i className="bi bi-check-lg"></i></label>
                                            </div>
                                        </li>}</>
                                ))}
                            </ul>
                        </div>
                        {leaveType != undefined &&
                            <>
                                <div className="form-group mb-3 csmCalanderGroup">
                                    <label for="" className="text-primary"><i className="bi bi-calendar2-range-fill pe-2"></i> Select Leave Date</label>
                                    <div id="datePicker">
                                        <div className="">
                                            <DateRange
                                                editableDateInputs={true}
                                                onChange={handleRangeChange}
                                                moveRangeOnFirstSelection={false}
                                                ranges={state}
                                                showPreview={true}
                                                rangeColors={['#F47320']}
                                            />
                                        </div>
                                        {errors?.leaveDetails && (
                                            <CustomError text={errors.leaveDetails} />
                                        )}
                                    </div>
                                </div>
                                <div className="form-group mb-3">
                                    <ul className="typeCheckList addDate">
                                        {leaveDetails && leaveDetails[0].leave_date !== '' &&
                                            leaveDetails.map((item, index) => (
                                                <>
                                                    <li>
                                                        <div className="date">
                                                            <span>{item?.leave_date}</span>
                                                        </div>
                                                    </li>
                                                    {item.leave_type == 'holiday' ?
                                                        <>
                                                            <li>
                                                                <div className="typeCheck">
                                                                    <input
                                                                        type="radio"
                                                                        id={`fullDay${index}`}
                                                                        name={`statusleaveType${index}`}
                                                                        className="d-none"
                                                                        checked={item.leave_type === 'Full Day'}
                                                                        readOnly
                                                                    //   onChange={() => handleRadioChange(index, 'Full Day')}
                                                                    />
                                                                    <label style={{ color: 'red' }} htmlFor={`fullDay${index}`}>Holiday <i className="bi bi-check-lg"></i></label>
                                                                </div>
                                                            </li>
                                                            <li>
                                                                <div className="typeCheck">
                                                                    <input
                                                                        type="radio"
                                                                        id={`firstHalfDay${index}`}
                                                                        name={`statusleaveType${index}`}
                                                                        className="d-none"
                                                                        checked={item.leave_status === 'First Half Day'}
                                                                    //   onChange={() => handleRadioChange(index, 'First Half Day')}
                                                                    />
                                                                    <label style={{ color: 'red' }} htmlFor={`firstHalfDay${index}`}>{item.leave_status == "full_day" ? 'Full Day' : item.leave_status == "first_half" ? "First Half day" : item.leave_status == "second_half" ? "Second Half Day" : ''}<i className="bi bi-check-lg"></i></label>
                                                                </div>
                                                            </li>
                                                            <li>
                                                                <div className="typeCheck">
                                                                    <input
                                                                        type="radio"
                                                                        id={`secondHalfDay${index}`}
                                                                        name={`statusleaveType${index}`}
                                                                        className="d-none"
                                                                        checked={item.leave_status === 'Second Half Day'}
                                                                    //   onChange={() => handleRadioChange(index, 'Second Half Day')}
                                                                    />
                                                                </div>
                                                            </li> </> :
                                                        <>
                                                            <li>
                                                                <div className="typeCheck">
                                                                    <input
                                                                        type="radio"
                                                                        id={`fullDay${index}`}
                                                                        name={`statusleaveType${index}`}
                                                                        className="d-none"
                                                                        checked={item.leave_status === 'Full Day'}
                                                                        onChange={() => handleRadioChange(index, 'Full Day')}
                                                                    />
                                                                    <label htmlFor={`fullDay${index}`}>Full Day <i className="bi bi-check-lg"></i></label>
                                                                </div>
                                                            </li>
                                                            <li>
                                                                <div className="typeCheck">
                                                                    <input
                                                                        type="radio"
                                                                        id={`firstHalfDay${index}`}
                                                                        name={`statusleaveType${index}`}
                                                                        className="d-none"
                                                                        checked={item.leave_status === 'First Half Day'}
                                                                        onChange={() => handleRadioChange(index, 'First Half Day')}
                                                                    />
                                                                    <label htmlFor={`firstHalfDay${index}`}>First Half Day <i className="bi bi-check-lg"></i></label>
                                                                </div>
                                                            </li>
                                                            <li>
                                                                <div className="typeCheck">
                                                                    <input
                                                                        type="radio"
                                                                        id={`secondHalfDay${index}`}
                                                                        name={`statusleaveType${index}`}
                                                                        className="d-none"
                                                                        checked={item.leave_status === 'Second Half Day'}
                                                                        onChange={() => handleRadioChange(index, 'Second Half Day')}
                                                                    />
                                                                    <label htmlFor={`secondHalfDay${index}`}>Second Half Day <i className="bi bi-check-lg"></i></label>
                                                                </div>
                                                            </li></>}
                                                </>
                                            ))}
                                    </ul>
                                </div>
                                {leaveDetails && leaveDetails[0]?.leave_date != '' &&
                                    <div className="form-group mb-3">
                                        <div className="row">
                                            <div className="col-6">
                                                <div className="durationBox">
                                                    <figure><img src="images/durationIcon.svg" width="16" alt="" /></figure>
                                                    <p><strong>Duration</strong> {leaveDetails && leaveDetails[0]?.leave_date != '' && leaveDetails?.length} Days</p>
                                                </div>
                                            </div>
                                            <div className="col-6">
                                                <div className="durationBox">
                                                    <figure><img src="images/leaveDaysIcon.svg" width="16" alt="" /></figure>
                                                    <p><strong>Leave Days</strong>{moment(startDate).format('MMM Do')} - {moment(endDate).format('MMM Do YYYY')}</p>
                                                </div>
                                            </div>
                                        </div>
                                    </div>}
                                <div className="form-group mb-3">
                                    <label for="" className="text-primary"><i className="bi bi-calendar2-range-fill pe-2"></i> Reason</label>
                                    <textarea className="form-control" placeholder="Enter Reason" value={reason} onChange={(e) => setReason(e.target.value)}></textarea>
                                    {errors?.reason && (
                                        <CustomError text={errors.reason} />
                                    )}
                                </div></>
                        }
                    </div>
                </div>
                <div className="offcanvas-footer">
                    <div className="d-flex align-items-center justify-content-end gap-3">
                        <CustomButton
                            text={"Cancel"}
                            handleClick={() => closeButton()}
                            classNames="border-0 p-0 bg-transparent cancelBtn"
                        />
                        <CustomButton
                            text={"Save"}
                            handleClick={() => submitApplyLeavefun()}
                            classNames="btn btn-org-gradient"
                            isLoading={sandwichLeavesDatesPending}
                        />
                    </div>
                </div>
            </div>

            {applyLeaveModel && (
                <ApproveRejectModal
                    onApproveReject={submitApplyLeave}
                    isShow={applyLeaveModel}
                    desc={`${sandwichDates.length > 0 ? `Sandwich policy automatic apply for this Date ${sandwichDates.join(", ")}` : "want to this Apply leave"}`}
                    btnRightLoading={applyLeavePending}
                    btnTextLeft="Cancel"
                />)
            }
        </>
    )
}

export default LeaveApplication