
import React, { useState, useEffect } from 'react'
import { Input, Label, Textarea } from '../../commanComponent';
import { useCustomTranslation } from '../../../translation';
import { CustomButton, CustomError } from '../../globalComponents';
import { checkMaxWordLimit, dateFormate, timeFormate, timeToHourMinSec } from '../../../utils/common';
import CustomSelect from '../../commanComponent/CustomSelect';
import { useSelector, useDispatch } from 'react-redux';
import { attendanceCorrectionActions, commonActions } from '../../../core/resource/actions';
import moment from 'moment';
import CorrectionCards from './CorrectionCards';


const CorrectionApplication = (props) => {
    const dispatch = useDispatch()
    const {
        openModel,
        handleOpenClose,
        toggleShowAllBreakModal,
        selectedCalenderData,
        updatedLogsData
    } = props
    const [attendanceCorrectionsData, setAttendanceCorrectionData] = useState('')
    const [appConstantData, setAppConstantData] = useState('')

    const {
        applyAttendanceCorrectionPending,
        applyAttendanceCorrectionRes,
        applyAttendanceCorrectionError,
    } = useSelector(state => state.attendanceCorrection)

    const {
        getAppConstantDataPending,
        getAppConstantDataRes,
        getAppConstantDataError,
    } = useSelector(state => state.common)
    const { t } = useCustomTranslation()
    const [errors, setErrors] = useState({})

    const [attendanceCorrectionFromData, setAttendanceCorrectionFromData] = useState({
        actualCheckIn: "",
        requestedCheckInTime: "",
        requestedCheckOutTime: '',
        requestedCheckInReason: '',
        requestedCheckOutReason: '',
        actualCheckOut: '',
        attendanceLogs: '',
        correctionStatus: null,
        actualBreak: "",
        correctedBreak: "",
        attendanceStatus: '',
        shiftEndTime: '',
        shiftStartTime: '',
        duration: '',
        attendanceDate: '',
        totalWorkingHours: '',
    })


    const handleFromChange = (e, fieldName) => {
        const name = fieldName || e.target?.name;
        const { value } = e?.target ? e.target : { value: e };
        setAttendanceCorrectionFromData(prevState => {
            const updatedState = {
                ...prevState,
                [name]: value,
            };
            return updatedState;
        });

        // Reset error for this field
        setErrors(prevErrors => ({
            ...prevErrors,
            [name]: undefined,
        }));
    }
    useEffect(() => {
        return (() => {
            resetFromData()
        })
    }, [])

    useEffect(() => {
        if (selectedCalenderData) {
            setAttendanceCorrectionData(selectedCalenderData)
            setAttendanceCorrectionFromData({
                actualCheckIn: selectedCalenderData?.first_check_in_time,
                requestedCheckInTime: "",
                requestedCheckOutTime: '',
                requestedCheckInReason: '',
                requestedCheckOutReason: '',
                actualCheckOut: selectedCalenderData?.last_check_out_time,
                attendanceLogs: '',
                correctionStatus: null,
                actualBreak: selectedCalenderData?.total_break_time,
                correctedBreak: "",
                attendanceStatus: selectedCalenderData?.attendance_status,
                shiftEndTime: selectedCalenderData?.shift_end_time,
                shiftStartTime: selectedCalenderData?.shift_start_time,
                duration: selectedCalenderData?.duration,
                attendanceDate: selectedCalenderData?.attendance_date,
                totalWorkingHours: selectedCalenderData?.total_working_hours,
            })
        }
    }, [selectedCalenderData])
    useEffect(() => {
        if (updatedLogsData) {
            setAttendanceCorrectionFromData(pre => ({
                ...pre,
                correctedBreak: updatedLogsData?.totalBreakTime

            }))
        }
    }, [updatedLogsData])


    const resetFromData = () => {
        setAttendanceCorrectionFromData({
            actualCheckIn: '',
            actualCheckOut: '',
            requestedCheckInTime: '',
            requestedCheckInReason: '',
            requestedCheckOutTime: '',
            requestedCheckOutReason: '',
            attendanceLogs: '',
            correctionStatus: null,
            attendanceStatus: '',
            shiftEndTime: '',
            shiftStartTime: '',
            duration: '',
            attendanceDate: '',
            totalWorkingHours: '',
        })
    }

    const handleSubmitCorrection = async () => {
        const {
            actualCheckIn,
            actualCheckOut,
            requestedCheckInTime,
            requestedCheckInReason,
            requestedCheckOutTime,
            requestedCheckOutReason,
            attendanceLogs,
            correctionStatus,
        } = attendanceCorrectionFromData
        let checkValid = checkValidationAttendanceCorrection()
        if (!checkValid) {
            const payload = {
                emp_id: attendanceCorrectionsData?.emp_id,
                date: attendanceCorrectionsData?.attendance_date,
                type: correctionStatus?.value,
                actual_check_in_time: actualCheckIn,
                actual_check_out_time: actualCheckOut,
                check_in_reason: requestedCheckInReason,
                check_out_reason: requestedCheckOutReason,
                correction_check_in_time: requestedCheckInTime || actualCheckIn,
                correction_check_out_time: requestedCheckOutTime || actualCheckOut,
                attendance_logs: []
            }
            dispatch(attendanceCorrectionActions.applyAttendanceCorrectionRequest(payload))
        }

    }
    /* Apply Attendance Correction */

    useEffect(() => {
        if (!applyAttendanceCorrectionPending) {
            if (applyAttendanceCorrectionError) {
                dispatch(attendanceCorrectionActions?.handleClearCorrectionRequest("HANDLE_CLEAR_APPLY_ATTENDANCE_CORRECTION"))
                return;
            }
            if (applyAttendanceCorrectionRes) {
                if (applyAttendanceCorrectionRes?.code === 200) {
                    dispatch(attendanceCorrectionActions?.handleClearCorrectionRequest("HANDLE_CLEAR_APPLY_ATTENDANCE_CORRECTION"))
                    handleOpenClose()
                    resetFromData()
                }
            }
        }
    }, [
        applyAttendanceCorrectionRes,
        applyAttendanceCorrectionError,
        applyAttendanceCorrectionPending
    ]);
    /* check time with in shift*/
    const isTimeWithinShift = (time, startTime, endTime) => {
        return moment(timeFormate(time,"HH:mm:ss"), "HH:mm:ss").isBetween(
            moment(timeToHourMinSec(startTime), "HH:mm:ss"),
            moment(timeToHourMinSec(endTime), "HH:mm:ss"),
            null,
            '[]'
        );
    };

    /* check valid Reason length */
    const validateReason = (reason, field) => {
        if (!reason) {
            setErrors(prevErrors => ({
                ...prevErrors,
                [field]: "Reason is required",
            }));
            return false;
        }
        if (checkMaxWordLimit(reason, 4)) {
            setErrors(prevErrors => ({
                ...prevErrors,
                [field]: "Maximum 5 words allowed in reason",
            }));
            return false;
        }
        return true;
    };

    /*check Validation Attendance Correction */
    const checkValidationAttendanceCorrection = () => {
        const {
            requestedCheckInTime,
            requestedCheckInReason,
            requestedCheckOutTime,
            requestedCheckOutReason,
            shiftEndTime,
            shiftStartTime,
            correctionStatus,
        } = attendanceCorrectionFromData;

        let hasError = false;
        if (!correctionStatus) {
            setErrors(prevErrors => ({
                ...prevErrors,
                correctionStatus: `Correction status  required`,
            }));
            hasError = true;
        }

        // Validate Check-In Time
        if (requestedCheckInTime) {
            if (!isTimeWithinShift(requestedCheckInTime, shiftStartTime, shiftEndTime)) {
                setErrors(prevErrors => ({
                    ...prevErrors,
                    requestedCheckInTime: `Select requested check-in time between ${shiftStartTime} and ${shiftEndTime}`,
                }));
                hasError = true;
            } else if (!validateReason(requestedCheckInReason, "requestedCheckInReason")) {
                hasError = true;
            }
        }

        // Validate Check-Out Time
        if (requestedCheckOutTime) {
            if (!isTimeWithinShift(requestedCheckOutTime, shiftStartTime, shiftEndTime)) {
                setErrors(prevErrors => ({
                    ...prevErrors,
                    requestedCheckOutTime: `Select requested check-out time between ${shiftStartTime} and ${shiftEndTime}`,
                }));
                hasError = true;
            } else if (!validateReason(requestedCheckOutReason, "requestedCheckOutReason")) {
                hasError = true;
            }
        }

        return hasError;
    };

    /*Get App Constant Data */
    useEffect(() => {
        getAppConstantData()
    }, [])
    const getAppConstantData = () => {
        if (getAppConstantDataRes && getAppConstantDataRes?.data) {
            setAppConstantData(getAppConstantDataRes?.data)
        } else {
            dispatch(commonActions.getAppConstantDataRequest())
        }
    }
    useEffect(() => {
        if (!getAppConstantDataPending) {
            if (getAppConstantDataError) {
                return;
            }
            if (getAppConstantDataRes) {
                if (getAppConstantDataRes?.code === 200) {
                    setAppConstantData(getAppConstantDataRes?.data)
                }
            }
        }
    }, [
        getAppConstantDataRes,
        getAppConstantDataError,
        getAppConstantDataPending
    ]);


    return (
        <>
            <div
                className={`offcanvas offcanvas-end applyLeaveCanvas ${openModel ? 'show' : ''}`}
                tabIndex={-1}
                id="attendanceCorrection"
                style={{
                    visibility: openModel ? 'visible' : 'hidden',
                    width: "800px"
                }}
            >
                <div className="offcanvas-header">
                    <h5 className="offcanvas-title  fs-6 fw-bold text-primary">
                        Attendance Correction
                    </h5>
                    <button
                        type="button"
                        className="btn-close"
                        data-bs-dismiss="offcanvas"
                        aria-label="Close"
                        onClick={() => { handleOpenClose() }}
                    />
                </div>
                <div className="offcanvas-body">
                    <div className="formPanel">
                        <CorrectionCards correctionData={selectedCalenderData} />
                        <div className="row">
                            <div className="col-md-3">
                                <Label className="text-primary mt-2"
                                    text={t("labels.correctionStatus")}
                                    required={true}
                                />
                            </div>
                            <div className="col-md-6">
                                <CustomSelect
                                    data={appConstantData?.dayType}
                                    placeholder={t("placeholders.correctionStatus")}
                                    value={attendanceCorrectionFromData?.correctionStatus || ''}
                                    handleChange={(selectedOption) => handleFromChange(selectedOption, "correctionStatus")}
                                    name="correctionStatus"
                                />
                                {errors?.correctionStatus && (
                                    <CustomError text={errors.correctionStatus} />
                                )}
                            </div>
                        </div>
                        <hr />
                        <div className="row">

                            <div className="col-md-6">
                                <div className="form-group mb-2">
                                    <Label className="text-primary"
                                        text={t("labels.actualCheckIn")}
                                    // required={true} 
                                    />
                                    <Input
                                        type="time"
                                        value={timeFormate(attendanceCorrectionFromData?.actualCheckIn,"YYYY-MM-DD HH:mm:ss")}
                                        name="actualCheckIn"
                                        onChange={(e) => handleFromChange(e)}
                                        min="09:30:00"
                                        step="2"
                                        readOnly={true}
                                    />
                                </div>
                            </div>
                            <div className="col-md-6">
                                <div className="form-group mb-2">
                                    <Label className="text-primary"
                                        text={t("labels.requestedCheckIn")}
                                    // required={true} 
                                    />
                                    <Input
                                        type="time"
                                        value={attendanceCorrectionFromData?.requestedCheckInTime}
                                        name="requestedCheckInTime"
                                        onChange={(e) => handleFromChange(e)}
                                        min="09:30:00"
                                        step="2"
                                    />
                                    {errors?.requestedCheckInTime && (
                                        <CustomError text={errors.requestedCheckInTime} />
                                    )}
                                </div>
                            </div>
                            <div className="col-md-12">
                                <div className="form-group mb-2">
                                    <Textarea
                                        placeholder={t("placeholders.requestCheckInReason")}
                                        onChange={(e) => handleFromChange(e)}
                                        value={attendanceCorrectionFromData?.requestedCheckInReason}
                                        name="requestedCheckInReason"
                                    />
                                    {errors?.requestedCheckInReason && (
                                        <CustomError text={errors.requestedCheckInReason} />
                                    )}

                                </div>
                            </div>
                        </div>
                        <hr />
                        <div className="row">
                            <div className="col-md-6">
                                <div className="form-group mb-2">
                                    <Label
                                        className="text-primary"
                                        text={t("labels.actualCheckOut")}
                                    // required={true}
                                    />
                                    <Input
                                        type="time"
                                        value={timeFormate(attendanceCorrectionFromData?.actualCheckOut,"YYYY-MM-DD HH:mm:ss")}
                                        name="actualCheckOut"
                                        onChange={(e) => handleFromChange(e)}
                                        max="18:30:00"
                                        step="2"
                                        readOnly={true}
                                    />
                                </div>
                            </div>
                            <div className="col-md-6">
                                <div className="form-group mb-2">
                                    <Label
                                        className="text-primary"
                                        text={t("labels.requestedCheckOut")}
                                    // required={true}
                                    />
                                    <Input
                                        type="time"
                                        value={attendanceCorrectionFromData?.requestedCheckOutTime}
                                        name="requestedCheckOutTime"
                                        onChange={(e) => handleFromChange(e)}
                                        max="18:30:00"
                                        step="2"
                                    />


                                </div>
                            </div>
                            <div className="col-md-12">
                                <div className="form-group mb-2">
                                    <Textarea
                                        placeholder={t("placeholders.requestCheckOutReason")}
                                        onChange={(e) => handleFromChange(e)}
                                        value={attendanceCorrectionFromData?.requestedCheckOutReason}
                                        name="requestedCheckOutReason"
                                    />
                                    {errors?.requestedCheckOutReason && (
                                        <CustomError text={errors.requestedCheckOutReason} />
                                    )}
                                </div>
                            </div>
                        </div>
                        <hr />
                        <div className="row">
                            <div className="col-md-6">
                                <div className="form-group mb-2">
                                    <Label
                                        className="text-primary"
                                        text={t("labels.actualBreak")}
                                    // required={true}
                                    />
                                    <Input
                                        type="time"
                                        value={attendanceCorrectionFromData?.actualBreak}
                                        name="actualBreak"
                                        onChange={(e) => handleFromChange(e)}
                                        step="2"
                                    // readOnly={true}
                                    />
                                </div>
                            </div>
                            <div className="col-md-6">
                                <div className="form-group mb-2">
                                    <Label
                                        className="text-primary"
                                        text={t("labels.correctedBreak")}
                                    />

                                    <Input
                                        type="time"
                                        value={attendanceCorrectionFromData?.correctedBreak}
                                        name="correctedBreak"
                                        onChange={(e) => handleFromChange(e)}
                                        step="2"

                                    />

                                </div>
                            </div>
                        </div>
                        <div className="form-group">
                            <CustomButton
                                classNames="btn btn-primary gradientBg w-100"
                                handleClick={() => {
                                    toggleShowAllBreakModal();
                                }}
                                text={t("btns.showAllBreak")}
                            />
                        </div>
                    </div>
                </div>
                <div className="offcanvas-footer">
                    <div className="d-flex align-items-center justify-content-end gap-3">
                        <CustomButton
                            classNames="border-0 p-0 bg-transparent cancelBtn"
                            handleClick={() => handleOpenClose()}
                            text={t("btns.cancel")}
                        />
                        <CustomButton
                            classNames="btn btn-org-gradient"
                            handleClick={() => handleSubmitCorrection()}
                            text={t("btns.submit")}
                        />
                    </div>
                </div>
            </div>

        </>
    )
}

export default CorrectionApplication