
import React, { useState, useEffect } from 'react'
import { CustomButton, CustomDateRangePicker, CustomError } from '../../globalComponents'
import { useDispatch, useSelector } from 'react-redux'
import { leaveAction } from '../../../core/resource/actions';
import { toast } from 'react-toastify';
import { DateRangePicker, DateRange } from 'react-date-range';
import 'react-date-range/dist/styles.css';
import 'react-date-range/dist/theme/default.css';
import { addDays } from 'date-fns';
import '../leaveApplication/LeaveCalendar.css';
import moment from 'moment';
import { Icon, Input, Label, Textarea } from '../../commanComponent';

const TourApplication = (props) => {
    const dispatch = useDispatch()
    const { isOpenModal, openTourApplicationModel } = props
    const {
        applyTourPending,
        applyTourRes,
        applyTourError
    } = useSelector(state => state.shift)

    const [startDate, setStartDate] = useState('')
    const [endDate, setEndDate] = useState("")
    const [tourFormData, setTourFormData] = useState({
        visitingDes: '',
        reason: '',
    })
    const [errors, setErrors] = useState('')

    const [range, setRange] = useState([
        {
            startDate: new Date(),
            endDate: new Date(),
            key: 'selection'
        }
    ]);
    const [holiday, setHoliya] = useState([
        { holiday_date: '2024-10-08', holiday_type: 'full_day' },
        { holiday_date: '2024-10-15', holiday_type: 'full_day' },
        { holiday_date: '2024-10-20', holiday_type: 'first_half' },
    ])
    const [tourDetails, setTourDetails] = useState([
        { tour_date: '', tour_status: '' },
    ]);

    const handleRadioChange = (index, value) => {
        const updatedLeaveDetails = tourDetails.map((item, i) =>
            i === index ? { ...item, tour_status: value } : item
        );
        setTourDetails(updatedLeaveDetails);
    };
    const handleRangeChange = (item) => {
        const { startDate, endDate } = item.selection;
        setRange([item.selection]);
        setStartDate(moment(startDate).format('YYYY-MM-DD'));
        setEndDate(moment(endDate).format('YYYY-MM-DD'));
    };
    useEffect(() => {
        createLeave()
    }, [endDate, startDate])
    const createLeave = () => {
        const start = new Date(moment(startDate).format('MM-DD-YYYY'));
        const end = new Date(moment(endDate).format('MM-DD-YYYY'));
        let loop = new Date(start);
        let i = 0;
        let newArr = [{ tour_date: '', tour_status: '' }];
        while (loop <= end) {
            var day = moment(loop).day();
            if (
                day === 1 ||
                day === 2 ||
                day === 3 ||
                day === 4 ||
                day === 5 ||
                day === 6 ||
                day === 0
            ) {
                if (holiday.some(item => moment(item.holiday_date).format("YYYY-MM-DD") === moment(loop).format('YYYY-MM-DD'))) {
                    let getHoliday = holiday.filter(item => moment(item.holiday_date).format('DD-MM-YYYY') === moment(loop).format('DD-MM-YYYY'))
                    newArr[i].tour_date = moment(loop).format('YYYY-MM-DD');
                    newArr[i].tour_type = "holiday"
                    newArr[i].tour_status = getHoliday[0].holiday_type;
                    setTourDetails(newArr);
                    let newArr1 = newArr;
                    newArr = [
                        ...newArr1,
                        { tour_date: '', tour_status: '' },
                    ];
                    let newDate = loop.setDate(loop.getDate() + 1);
                    loop = new Date(newDate);
                    i = i + 1;
                } else {
                    newArr[i].tour_date = moment(loop).format('YYYY-MM-DD');
                    newArr[i].tour_status = "Full Day";
                    setTourDetails(newArr);
                    let newArr1 = newArr;
                    newArr = [
                        ...newArr1,
                        { tour_date: '', tour_status: '' },
                    ];
                    let newDate = loop.setDate(loop.getDate() + 1);
                    loop = new Date(newDate);
                    i = i + 1;
                }
            } else {
                let newDate1 = loop.setDate(loop.getDate() + 1);
                loop = new Date(newDate1);
            }
        }
    };
    useEffect(() => {
        if (!applyTourPending) {
            if (applyTourError) {
                return;
            }
            if (applyTourRes) {
                if (applyTourRes?.data?.code === 200) {
                    dispatch(leaveAction.handleClearLeaveStore("HANDLE_CLEAR_APPLY_Tour_STORE"))
                }
            }
        }
    }, [
        applyTourRes,
        applyTourError,
        applyTourPending
    ]);

    const submitApplyTour = () => {
        let data = {
            tourDetails: tourDetails,
            reason: tourFormData?.reason,
            destination: tourFormData?.visitingDes
        }
        dispatch(leaveAction.applyTourRequest(data))
    }

    const closeButton = () => {
        openTourApplicationModel()
        resetFunction()
    }

    const resetFunction = () => {
        setTourFormData({
            visitingDes: '',
            reason: '',
        })
        setStartDate(' ')
        setEndDate(' ')
        setTourDetails([{
            tour_date: '', tour_status: ''
        }])
        setRange([{
            startDate: new Date(),
            endDate: new Date(),
            key: 'selection'
        }])
    }

    const handleFromChange = (e, fieldName) => {
        const name = fieldName || e.target?.name;
        const { value, type, checked } = e?.target ? e?.target : { value: e };
        setTourFormData(prevState => {
            let updatedState = {
                ...prevState,
                [name]: type === 'checkbox' ? checked : value,
            };
            return updatedState;
        });
        setErrors(prevErrors => ({
            ...prevErrors,
            [name]: undefined,
        }));
    };

    return (
        <>

            <div className={`offcanvas offcanvas-end applyLeaveCanvas ${isOpenModal ? 'show' : ''}`}

                tabIndex={-1}
                style={{
                    visibility: isOpenModal ? 'visible' : 'hidden',
                    width: "800px"
                }}
            >
                <div className="offcanvas-header">
                    <h5 className="offcanvas-title  fs-5 fw-bold text-primary">Tour Application</h5>
                    <button type="button" className="btn-close" data-bs-dismiss="offcanvas" aria-label="Close"
                        onClick={() => closeButton()}
                    />
                </div>
                <div class="offcanvas-body">
                    <div class="formPanel">
                        <div class="form-group mb-3">
                            <label for="" class="text-primary"><i class="bi bi-calendar2-range-fill pe-2"></i> Visiting
                                Destination</label>
                            <Input
                                type={"text"}
                                name={'visitingDes'}
                                placeholder={'Enter Visiting Destination'}
                                onChange={handleFromChange}
                                value={tourFormData?.visitingDes}
                            />


                        </div>
                        <div class="form-group mb-3 csmCalanderGroup">
                            <label for="" class="text-primary"><i class="bi bi-calendar2-range-fill pe-2"></i> Select Tour Date</label>
                            <div className="">
                                <DateRange
                                    editableDateInputs={true}
                                    onChange={handleRangeChange}
                                    moveRangeOnFirstSelection={false}
                                    ranges={range}
                                    showPreview={true}
                                    rangeColors={['#F47320']}
                                />
                            </div>
                        </div>

                        <div className="form-group mb-3">
                            <ul className="typeCheckList addDate">
                                {tourDetails && tourDetails[0].tour_date !== '' &&
                                    tourDetails.map((item, index) => (
                                        <>
                                            <li>
                                                <div className="date">
                                                    <span>{item?.tour_date}</span>
                                                </div>
                                            </li>
                                            {item.tour_type === 'holiday' ?
                                                <>
                                                    <li>
                                                        <div className="typeCheck">
                                                            <input
                                                                type="radio"
                                                                id={`fullDay${index}`}
                                                                name={`statustourType${index}`}
                                                                className="d-none"
                                                                checked={item.tour_type === 'Full Day'}
                                                                readOnly
                                                            //   onChange={() => handleRadioChange(index, 'Full Day')}
                                                            />
                                                            <label style={{ color: 'red' }} htmlFor={`fullDay${index}`}>Holiday <i className="bi bi-check-lg"></i></label>
                                                        </div>
                                                    </li>
                                                    <li>
                                                        <div className="typeCheck">
                                                            <input
                                                                type="radio"
                                                                id={`firstHalfDay${index}`}
                                                                name={`statustourType${index}`}
                                                                className="d-none"
                                                                checked={item.tour_status === 'First Half Day'}
                                                            //   onChange={() => handleRadioChange(index, 'First Half Day')}
                                                            />
                                                            <label style={{ color: 'red' }} htmlFor={`firstHalfDay${index}`}>{item.tour_status === "full_day" ? 'Full Day' : item.tour_status === "first_half" ? "First Half day" : item.tour_status === "second_half" ? "Second Half Day" : ''}<i className="bi bi-check-lg"></i></label>
                                                        </div>
                                                    </li>
                                                    <li>
                                                        <div className="typeCheck">
                                                            <input
                                                                type="radio"
                                                                id={`secondHalfDay${index}`}
                                                                name={`statustourType${index}`}
                                                                className="d-none"
                                                                checked={item.tour_status === 'Second Half Day'}
                                                            //   onChange={() => handleRadioChange(index, 'Second Half Day')}
                                                            />
                                                        </div>
                                                    </li> </> :
                                                <>
                                                    <li>
                                                        <div className="typeCheck">
                                                            <input
                                                                type="radio"
                                                                id={`fullDay${index}`}
                                                                name={`statustourType${index}`}
                                                                className="d-none"
                                                                checked={item.tour_status === 'Full Day'}
                                                                onChange={() => handleRadioChange(index, 'Full Day')}
                                                            />
                                                            <label htmlFor={`fullDay${index}`}>Full Day <i className="bi bi-check-lg"></i></label>
                                                        </div>
                                                    </li>
                                                    <li>
                                                        <div className="typeCheck">
                                                            <input
                                                                type="radio"
                                                                id={`firstHalfDay${index}`}
                                                                name={`statustourType${index}`}
                                                                className="d-none"
                                                                checked={item.tour_status === 'First Half Day'}
                                                                onChange={() => handleRadioChange(index, 'First Half Day')}
                                                            />
                                                            <label htmlFor={`firstHalfDay${index}`}>First Half Day <i className="bi bi-check-lg"></i></label>
                                                        </div>
                                                    </li>
                                                    <li>
                                                        <div className="typeCheck">
                                                            <input
                                                                type="radio"
                                                                id={`secondHalfDay${index}`}
                                                                name={`statustourType${index}`}
                                                                className="d-none"
                                                                checked={item.tour_status === 'Second Half Day'}
                                                                onChange={() => handleRadioChange(index, 'Second Half Day')}
                                                            />
                                                            <label htmlFor={`secondHalfDay${index}`}>Second Half Day <i className="bi bi-check-lg"></i></label>
                                                        </div>
                                                    </li></>}
                                        </>
                                    ))}
                            </ul>
                        </div>
                        {tourDetails && tourDetails[0]?.tour_date !== '' &&
                            <div className="form-group mb-3">
                                <div className="row">
                                    <div className="col-6">
                                        <div className="durationBox">
                                            <figure><img src="images/durationIcon.svg" width="16" alt="" /></figure>
                                            <p><strong>Duration</strong> {tourDetails && tourDetails[0]?.tour_date !== '' && tourDetails?.length} Days</p>
                                        </div>
                                    </div>
                                    <div className="col-6">
                                        <div className="durationBox">
                                            <figure><img src="images/leaveDaysIcon.svg" width="16" alt="" /></figure>
                                            <p><strong>Tour Days</strong>{moment(startDate).format('MMM Do')} - {moment(endDate).format('MMM Do YYYY')}</p>
                                        </div>
                                    </div>
                                </div>
                            </div>}


                        <div class="form-group mb-3">
                            <Icon className={"bi bi-calendar2-range-fill pe-1 text-primary"} />
                            <Label text={"Reason"} />
                            <Textarea
                                name='reason'
                                placeholder="Enter Reason"
                                onChange={handleFromChange}
                            />
                            {errors?.reason && (
                                <CustomError text={errors.reason} />
                            )}
                        </div>
                    </div>
                </div>
                <div className="offcanvas-footer">
                    <div className="d-flex align-items-center justify-content-end gap-3">
                        <button type="button" className="border-0 p-0 bg-transparent cancelBtn" onClick={() => closeButton()}>Cancel</button>
                        <CustomButton
                            text={"save"}
                            classNames={"btn btn-org-gradient"}
                            handleClick={() => submitApplyTour()}
                            // isLoading={}
                        />

                    </div>
                </div>
            </div>
            {/* {isOpenModal && <div className="modal-backdrop fade show"></div>} */}
        </>
    )
}

export default TourApplication